.form{
    display: flex;
    flex-direction: column;
    width: 300px;
    height: 160px;
    margin-bottom: 20px;
    border: 1.5px solid#000;
}

.label{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-top: 5px;
    width: 150px;
}

.input{
    margin-top: 5px;
}

.input:hover{
    box-shadow: 0 0 10px rgb(3, 102, 214);
}


.btn{
    margin-top: 15px;
    width: 115px;
    height: 20px;
    border-radius: 8px;
    border: 2px solid #d3d3d3;
    background-color: white;
    margin-left: 10px;
}

.btn:hover{
    background-color: #0366d6;
    border: 2px solid #0366d6;
    cursor: pointer;
}